const shareButtons = document.querySelectorAll('.share__button');

shareButtons.forEach(button => {
    const shareList = button.querySelector('.share-links');
    button.addEventListener('click', () => {
        if (shareList.classList.contains('visible')) {
            shareList.classList.remove('visible');
        } else {
            shareList.classList.add('visible');
        }
    });

})
